<template>
  <el-container class="prepayment myhospital">
    <el-header style="height: 35px;">
      <div class="grid-content fonttop">
        <span>就诊人：</span>
        <span>*振</span>
        <span style="margin:10px">0005674</span>
        <span style="float: right;line-height: 15px;">
          <el-button class="cut">切换</el-button>
        </span>
      </div>
    </el-header>
    <el-main>
      <el-card class="box-card myhospital">
        <el-row>
          <el-col :span="4">
            <div class="grid-content bg-purple" style="margin-top:22px;">
              <el-avatar>
                <img src="../../assets/img/woment.png" alt />
              </el-avatar>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="grid-content bg-purple-light" style="text-align: left;">
              <p class="font_bold">
                <span>{{clientName}}</span>
                <i class="el-icon-female" style="color:red;"></i>
              </p>
              <p class="myhospital_time_ft">
                <span>{{clientAge}}岁</span>
                <span>{{clientRoom}}</span>
              </p>
              <p class="myhospital_time_ft">
                <span>{{clientTime}}入院</span>
              </p>
              <p></p>
              <p></p>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple prepayment_bt" style="margin-top:26px;">
              <router-link tag="span" to="/shopcart/Prepay/perpayrecord">
                <el-button type="primary" plain>充值记录</el-button>
              </router-link>
            </div>
          </el-col>
        </el-row>
        <el-divider class="prepayment_hr"></el-divider>
        <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark prepayment_Ip">
              <p>预交金充值金额</p>
              <div>
                ￥
                <el-input
                  oninput="value=value.replace(/[^\d.]/g,'')"
                  v-model="input"
                  style=" font-size: 30px;"
                  @input="changeInput()"
                  maxlength="10"
                ></el-input>
              </div>
              <el-divider class="prepayment_hr"></el-divider>

              <p class="myhospital_time_ft" style="color:#999">
                请属于小于
                <span>{{jt}}</span>的金额
              </p>
            </div>
            <!-- <el-button v-if="isOk" type="primary" :disabled="isDisabled" class="prepayment_Bt"></el-button> -->
            <!-- <el-button v-if="!isOk" type="primary" disabled class="prepayment_Bt">确定支付</el-button> -->
            <el-button type="primary" @click="btu()" class="prepayment_Bt" :disabled="dis">确定支付</el-button>
          </el-col>
        </el-row>
      </el-card>
    </el-main>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
export default {
  data() {
    return {
      clientName: "刘溜",
      clientAge: "21",
      clientRoom: "VIP病区科室",
      clientTime: "2020.4.13 08:25",
      input: "",
      jt: "",
      dis: true
      // isOk:false
    };
  },

  // 输入监听

  // oninput="value=value.replace(/[^\d.]/g,'')" 只能输入金额

  methods: {
    changeInput() {
      if (this.input > 0) {
        console.log(this.input);
        this.dis = false;
      } else if (this.input == 0) {
        this.dis = true;
      }
    },
    btu() {
      alert(this.input);
    }
  }
};
</script>



